import Exercice from '../Exercice.js'
import { context } from '../../modules/context.js'
import { listeQuestionsToContenu, randint, combinaisonListes, miseEnEvidence } from '../../modules/outils.js'
import { ajouteChampTexte, setReponse } from '../../modules/gestionInteractif.js'
// on importait amcReady de 5L10 cf commit cf25dab mais mieux vaut le d√©clarer explicitement

export const titre = 'Additions et de soustractions'
export const interactifReady = true
export const amcReady = true
export const amcType = 4


/**
 * Additions et/ou soustractions classique et/ou √† trou.
 *
 * Par d√©faut c'est un m√©lange d'additions, soustractions avec et sans trou avec des nombres jusqu'√† 20.
 * @Auteur R√©mi Angot
 * R√©f√©rence CM000
 */
export default function TablesAdditionsSoustractions () {
  Exercice.call(this) // H√©ritage de la classe Exercice()
  this.sup = 20
  this.sup2 = 6 // additions|additions √† trous|soustractions|soustractions √† trous|m√©lange sans trou|m√©lange avec trou
  this.titre = titre
  this.consigne = 'Calculer'
  this.spacing = 2
  this.tailleDiaporama = 100
  this.interactif = true
  this.interactifReady = interactifReady
  this.amcType = amcType
  this.amcReady = amcReady

  this.nouvelleVersion = function () {
    this.sup = parseInt(this.sup)
    this.sup2 = parseInt(this.sup2)
    this.listeQuestions = [] // Liste de questions
    this.listeCorrections = [] // Liste de questions corrig√©es
    let listeTypeDeQuestions = []
    if (this.sup2 === 1) {
      listeTypeDeQuestions = combinaisonListes(
        ['addition'],
        this.nbQuestions
      )
    }
    if (this.sup2 === 2) {
      listeTypeDeQuestions = combinaisonListes(
        ['addition_a_trou'],
        this.nbQuestions
      )
    }
    if (this.sup2 === 3) {
      listeTypeDeQuestions = combinaisonListes(
        ['soustraction'],
        this.nbQuestions
      )
    }
    if (this.sup2 === 4) {
      listeTypeDeQuestions = combinaisonListes(
        ['soustraction_a_trou'],
        this.nbQuestions
      )
    }
    if (this.sup2 === 5) {
      listeTypeDeQuestions = combinaisonListes(
        ['addition', 'soustraction'],
        this.nbQuestions
      )
    }
    if (this.sup2 === 6) {
      listeTypeDeQuestions = combinaisonListes(
        ['addition', 'addition_a_trou', 'soustraction', 'soustraction_a_trou'],
        this.nbQuestions
      )
    }
    for (let i = 0, a, b, texte, texteCorr; i < this.nbQuestions; i++) {
      a = randint(2, this.sup)
      b = randint(2, this.sup)

      switch (listeTypeDeQuestions[i]) {
        case 'addition':
          texte = `$${a} + ${b} = \\dotfill$`
          if (this.interactif) {
            texte = ajouteChampTexte(this, i, {
              texte: `$${a} + ${b} = $`
            })
          }
          texteCorr = `$${a} + ${b} = ${a + b}$`
          setReponse(this, i, a + b)
          break
        case 'addition_a_trou':
          texte = `$${a} + \\ldots\\ldots = ${a + b}$`
          if (this.interactif) {
            texte = ajouteChampTexte(this, i, {
              texte: `$${a}~+ $`,
              texteApres: `$= ${a + b}$`
            })
          }
          texteCorr = `$${a} + ${miseEnEvidence(b)} = ${a + b}$`
          setReponse(this, i, b)
          break
        case 'soustraction':
          if (a === b) {
            a = randint(2, this.sup, b)
          }
          if (a < b) {
            b = [a, (a = b)][0] // √©change les variables a et b
          }
          texte = `$${a} - ${b} = \\dotfill$`
          if (this.interactif) {
            texte = ajouteChampTexte(this, i, {
              texte: `$${a} - ${b} = $`
            })
          }
          texteCorr = `$${a} - ${b} = ${a - b}$`
          setReponse(this, i, a - b)
          break
        case 'soustraction_a_trou':
          if (a === b) {
            a = randint(2, this.sup, b)
          }
          if (a < b) {
            b = [a, (a = b)][0] // √©change les variables a et b
          }
          texte = `$${a} - \\ldots\\ldots = ${a - b}$`
          if (this.interactif) {
            texte = ajouteChampTexte(this, i, {
              texte: `$${a}~- $`,
              texteApres: `$= ${a - b}$`
            })
          }
          texteCorr = `$${a} - ${miseEnEvidence(b)} = ${a - b}$`
          setReponse(this, i, b)
          break
      }

      if (context.isDiaporama) {
        texte = texte.replace('= \\dotfill', '')
      }
      this.autoCorrection[i].reponse.param = { digits: Math.ceil(2 * this.sup / 100), decimals: 0, signe: false, exposant_nb_chiffres: 0, exposant_signe: false, approx: 0 }
      this.listeQuestions.push(texte)
      this.listeCorrections.push(texteCorr)
    }
    listeQuestionsToContenu(this)
  }
  this.besoinFormulaireNumerique = ['Valeur maximale', 9999] // Texte, tooltip
  this.besoinFormulaire2Numerique = [
    'Style de questions',
    6,
    '1 : Additions\n2: Additions √† trous\n3: Soustractions\n4 : Soustractions √† trous\n5 : Additions et soustractions \n6 : Additions et soustractions avec ou sans trous'
  ]
}
